.product__header {
  @include __subgradient(#ffe18b, #ff9da9);
}

.product__header .sub__header-character {
  img {
    position: relative;
    z-index: 3;
  }
  &:after {
    content: '';
    display: block;
    position: absolute;
    top: 50%;
    right: -198px;
    margin-top: -159.5px;
    width: 1572px;
    height: 391px;
    background: url(../img/product/character_bg.png) no-repeat 0 0;
  }
}

.product__game {
  padding: 100px 0 100px;
}
.product__game:hover .product__game-title img {
  @extend %ani-rubber;
}
.product__game-title {
  text-align: center;
  padding-bottom: 40px;
}

.product__game-list {
  @extend %clearfix;
  font-size: 0;
  max-width: 280px * 4;
  margin: 0 auto;
  text-align: left;
}
@media (max-width: 1140px) {
  .product__game-list {
    max-width: 280px * 3;
  }
}

.product__game-cell {
  display: inline-block;
  width: 260px;
  height: 385px;
  padding: 0 10px;
}

.product__game-cell-btn {
  display: block;
  width: 260px;
  height: 260px;
  position: relative;
  border-radius: 50%;
  background-color: #ffe18b;
}
.product__game-cell-btn-thumb-tb {
  position: absolute;
  display: table;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  .thumb-cell {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
  }
  img {
    min-width: 100%;
  }
}
.product__game-cell-btn-border {
  box-sizing: border-box;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 1px solid #e9e9e9;
  transition: border 0.25s $easeinout;
}
.product__game-cell-btn:hover .product__game-cell-btn-border,
.open .product__game-cell-btn-border {
  border: #ffaaa3 solid 5px;
}
.product__game-cell-platform {
  text-align: center;
  padding: 20px 0 10px;
  color: #666666;
  font-size: $fs16;
}
.product__game-cell-title {
  text-align: center;
  color: #000000;
  font-size: $fs20;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 90%;
  margin: 0 auto;
  line-height: 1.5;
}
.product__game-cell .product__game-detail {
  display: none;
}
.product__game-detail {
  position: absolute;
  display: block;
  width: 2560px;
  left: 50%;
  bottom: 0;
  margin-left: -1280px;
  height: 100%;
  overflow: hidden;
}
.product__game-wrap {
  position: relative;
  display: block;
  display: flex;
  align-items: center;
  // justify-content: center;
  width: 100%;
  height: 100%;
}
.product__game-detail-bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: 50% 0;
  &.game--poker {
    background-image: url(../img/product/g1_bg.jpg);
  }
  &.game--matgo {
    background-image: url(../img/product/g2_bg.jpg);
  }
  &.game--sutda {
    background-image: url(../img/product/g3_bg.jpg);
  }
  &.game--blackjack {
    background-image: url(../img/product/g4_bg.jpg);
  }
  &.game--slot {
    background-image: url(../img/product/g5_bg.jpg);
  }
  &.game--capsa {
    background-image: url(../img/product/g6_bg.jpg);
  }
  &.game--baccarat {
    background-image: url(../img/product/g10_bg.jpg);
  }
  &.game--gostop2018 {
    background-image: url(../img/product/g12_bg.jpg);
  }
  &.game--gostopfree {
    background-image: url(../img/product/g13_bg.jpg);
  }
  &.game--hob {
    background-image: url(../img/product/g14_bg.jpg);
  }
  &.game--gostopcat {
    background-image: url(../img/product/g15_bg.jpg);
  }
  &.game--gostopdog {
    background-image: url(../img/product/g16_bg.jpg);
  }
  &.game--sbb {
    background-image: url(../img/product/g17_bg.jpg);
  }
  &.game--solitaire {
    background-image: url(../img/product/g18_bg.jpg);
  }
  &.game--crazyboat {
    background-image: url(../img/product/g19_bg.jpg);
  }
}
.product__game-detail-body {
  box-sizing: border-box;
  padding: 40px 0 40px 430px;
  max-width: 1100px;
  margin: 0 auto;
  font-size: $fs16;
  position: relative;
  z-index: 5;
  text-align: left;
  color: #fff;
  line-height: 1.6;
  @extend %word-break;
}
.txt--black .product__game-detail-body {
  color: #000;
}
.product__game-detail-platform {
  font-size: $fs16;
  @include __opacity(0.5);
  padding: 0px 0 5px;
}
.product__game-detail-title {
  font-size: $fs36;
}
.product__game-detail-desc {
  font-size: $fs16;
  font-weight: 300;
  padding: 15px 0 25px;
  @include __opacity(0.9);
}
.product__game-detail-btn {
  margin-left: -10px;
  display: block;
  text-indent: -99999px;
  width: 328px;
  height: 96px;
  background: url(../img/product/btn_play.png) no-repeat 0 0;
  background-size: 100%;
  border-radius: 96px;
  @include __btnshine(-35%, 125%, 1s);
}

.product__game-wrap {
  transition: transform $open-delay $easeinout;
  transform: translate(0, -100%);
}
.product__game-detail-platform,
.product__game-detail-title,
.product__game-detail-desc,
.product__game-detail-btnbox {
  transition: opacity 0.4s $easeout, transform 0.4s $easeout;
  @include __opacity(0);
  transform: translate(40px, 0);
}
.state--current {
  .product__game-wrap {
    z-index: 11;
    transform: translate(0, 0);
  }

  .product__game-detail-platform,
  .product__game-detail-title,
  .product__game-detail-desc,
  .product__game-detail-btnbox {
    @include __opacity(1);
    transform: translate(0, 0);
  }

  .product__game-detail-platform {
    @include __opacity(0.5);
    transition-delay: 0.6s;
  }
  .product__game-detail-title {
    transition-delay: 0.8s;
  }
  .product__game-detail-desc {
    @include __opacity(0.9);
    transition-delay: 1s;
  }
  .product__game-detail-btnbox {
    transition-delay: 1.2s;
  }
}

.state--before {
  .product__game-wrap {
    z-index: 10;
    transform: translate(0, 30%);
  }
  .product__game-detail-platform,
  .product__game-detail-title,
  .product__game-detail-desc,
  .product__game-detail-btnbox {
    transition: opacity 0.2s $easein, transform 0.2s $easein;
    @include __opacity(0);
    transform: translate(0, -30%);
  }
}
@media (max-width: 1100px) and (min-width: 761px) {
  .product__header .sub__header-character:after {
    display: none;
  }
  .product__game-list {
    max-width: 250px * 3;
    padding: 0;
  }
  .product__game-cell {
    width: 230px;
    height: 365px;
  }
  .product__game-cell-btn {
    width: 230px;
    height: 230px;
  }
  .product__game-cell-btn-thumb-tb img {
    max-width: 230px;
  }
  .product__game .open-block.active--ani {
    height: 350px;
  }
  .product__game-detail-bg {
    background-size: 75%;
    background-position: 355px 0;
  }
  .product__game-detail-body {
    // height: 350px;
    max-width: 730px;
    padding: 10px 0 10px 290px;
  }
  .product__game-detail-platform {
    padding: 0px 0 5px;
    font-size: 14px;
  }
  .product__game-detail-title {
    font-size: 24px;
  }
  .product__game-detail-desc {
    font-size: 13px;
  }
  .product__game-detail-btn {
    width: 208px;
    height: 59px;
  }
}

@media (max-width: 760px) {
  .product__game-wrap {
    align-items: start;
  }
  .product__header .sub__header-title .text--rollup:nth-child(1),
  .product__header .sub__header-title .text--rollup:nth-child(2) {
    display: inline-block;
  }
  .product__header .sub__header-title .text--rollup:nth-child(3) {
    clear: both;
  }
  .product__header .sub__header-character {
    margin: 30px auto 0;
    padding-bottom: 30px;
    width: 80%;
  }
  .product__header .sub__header-character:after {
    display: none;
  }
  .product__game {
    padding: 50px 0 50px;
  }
  .product__game-title {
    padding-bottom: 20px;
    img {
      height: 21px;
    }
  }
  .product__game-list {
    padding: 0;
  }
  .product__game-cell {
    box-sizing: border-box;
    width: 33%;
    height: 165px;
    padding: 0;
  }
  .open .product__game-cell-btn-border,
  .product__game-cell-btn:hover .product__game-cell-btn-border {
    border-width: 3px;
  }
  .product__game-cell-btn {
    overflow: hidden;
    width: 100px;
    height: 100px;
    margin: 0 auto;
  }
  .product__game-cell-btn-thumb-tb {
    overflow: hidden;
    img {
      max-width: 100px;
    }
  }
  .product__game-cell-platform {
    padding: 10px 0 5px;
    font-size: 11px;
  }
  .product__game-cell-title {
    font-size: 12px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  //open
  .product__game .open-block.active--ani {
    height: 0;
    padding-top: 120%;
    margin-bottom: 15px;
  }
  .product__game-detail {
    width: 100%;
    left: 0;
    top: 0;
    margin-left: 0;
  }
  .product__game-detail-bg {
    background-size: 100% auto;
    &.game--poker {
      background-color: #331e63;
      background-image: url(../img/product/g1_bg_m.jpg);
    }
    &.game--matgo {
      background-color: #20e2d7;
      background-image: url(../img/product/g2_bg_m.jpg);
    }
    &.game--sutda {
      background-color: #3d2013;
      background-image: url(../img/product/g3_bg_m.jpg);
    }
    &.game--blackjack {
      background-color: #400041;
      background-image: url(../img/product/g4_bg_m.jpg);
    }
    &.game--slot {
      background-color: #491e97;
      background-image: url(../img/product/g5_bg_m.jpg);
    }
    &.game--capsa {
      background-color: #4c0c19;
      background-image: url(../img/product/g6_bg_m.jpg);
    }
    &.game--baccarat {
      background-color: #38230c;
      background-image: url(../img/product/g10_bg_m.jpg);
    }
    &.game--gostop2018 {
      background-color: #4968b9;
      background-image: url(../img/product/g12_bg_m.jpg);
    }
    &.game--gostopfree {
      background-color: #4068c0;
      background-image: url(../img/product/g13_bg_m.jpg);
    }
    &.game--hob {
      background-color: #2e045a;
      background-image: url(../img/product/g14_bg_m.jpg);
    }
    &.game--gostopcat {
      background-color: #004b2f;
      background-image: url(../img/product/g15_bg_m.jpg);
    }
    &.game--gostopdog {
      background-color: #00c573;
      background-image: url(../img/product/g16_bg_m.jpg);
    }
    &.game--sbb {
      background-color: #071f41;
      background-image: url(../img/product/g17_bg_m.jpg);
    }
    &.game--solitaire {
      background-color: #171f32;
      background-image: url(../img/product/g18_bg_m.jpg);
    }
    &.game--crazyboat {
      background-color: #008bea;
      background-image: url(../img/product/g19_bg_m.jpg);
    }
  }
  .product__game-detail-body {
    padding: 51.5% 5% 0;
    text-align: center;
    max-width: 370px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .product__game-detail-platform {
    padding: 0;
    font-size: 11px;
  }
  .product__game-detail-title {
    font-size: 18px;
  }
  .product__game-detail-desc {
    font-size: 11px;
    line-height: 1.5;
    padding: 5px 0 10px;
  }
  .product__game-detail-btn {
    margin: 0 auto;
    width: 135px;
    height: 41px;
  }
  .product__game .open-block.active--ani:last-child {
    margin-bottom: -50px;
  }
}
@media (max-width: 760px) and (orientation: landscape) {
  .product__game .open-block.active--ani {
    height: 460px;
    padding-top: 0;
  }
  .product__game-detail-bg {
    background-size: auto 100%;
  }
  .product__game-detail-body {
    padding-top: 31%;
    max-width: 380px;
    margin: 0 auto;
  }
}
