.footer {
  position: relative;
  padding: 60px 40px;
  z-index: 10;
}

.footer_nps {
  display: inline-block;
  width: 205px;
  height: 44px;
  background: url(../img/foot_bi.png) no-repeat 0 0;
  text-indent: -9999px;
  background-size: 100%;
}
.footer__top {
  position: relative;
}
.footer__sns {
  position: absolute;
  right: 0;
  top: 0;
  font-size: 0;
  text-align: right;
  .footer__links a {
    position: relative;
    overflow: hidden;
    display: inline-block;
    width: 50px;
    height: 50px;
    margin-left: 7px;
    background-size: 100%;
    @include __btnshine(-50%, 150%, .8s);
  }
  .footer__link-facebook {
    background: url(../img/foot_f.png) no-repeat 0 0;
  }
  .footer__link-youtube {
    background: url(../img/foot_y.png) no-repeat 0 0;
  }
  .footer__link-palystore {
    background: url(../img/foot_p.png) no-repeat 0 0;
  }
}
.footer__links {
  position: absolute;
  right: 229px;
  top: 0;
  width: 200px;
}

.footer__top .familysite {
  position: absolute;
  right: 0;
  top: 0;
  height: $fam_height;
  .familysite__links {
    position: absolute;
    right: 0;
    bottom: 0;
    background-color: #fff;
  }
}

.footer__address {
  margin: 40px 0 30px;;
  font-size: $fs18;
  line-height: 1.3;
}
.footer__copy {
  font-size: $fs18;
  line-height: 1.3;
  i {
    font-size: $fs14;
    vertical-align: middle;
    position: relative;
    top: -4px;
    padding: 0 1em;
  }
}
@media (max-width: 1100px) and (min-width: 761px) {
  .footer_nps {
    width: 205px*.8;
    height: 44px*.8;
    position: relative;
    top: 3px;
  }
  .footer__sns .footer__links a {
    width: 40px;
    height: 40px;
    margin-left: 3px;
  }
  .footer__links {
    right: 174px;
  }
  .footer__top .familysite {
    position: relative;
    float: left;
    top: auto;
    width: 170px;
    height: 40px;
    right: auto;
    margin-left: 3px
  }
  .footer__top .familysite .familysite__links {
    transform: scale(0.77);
    transform-origin: 100% 100%;
  }
  .footer__address {
    font-size: 14px;
  }
  .footer__copy {
    font-size: 14px;
  }
}
@media (max-width: 760px) {
  .footer {
    padding: 20px 15px;
  }
  .footer_nps {
    width: 205px*.4;
    height: 44px*.4;
    position: relative;
    top: 3px;
  }
  .footer__sns {
  }
  .footer__sns .footer__links a {
    width: 25px;
    height: 25px;
    margin-left: 3px;
  }
  .footer__links {
    float: left;
    position: relative;
    right: auto;
    top: auto;
    width: auto;
  }
  .footer__top .familysite {
    position: relative;
    float: left;
    top: auto;
    width: 109px;
    height: 25px;
    right: auto;
    margin-left: 3px;
  }
  .footer__top .familysite .familysite__links {
    position: absolute;
    right: auto;
    bottom: auto;
    left: 0;
    bottom: 0;
    transform: scale(.49);
    transform-origin: 0 100%;
  }
  .footer__address {
    margin:30px 0 30px;
    font-size: 12px;
    letter-spacing: -1px;
  }
  .footer__copy {
    position: relative;
    font-size: 12px;
    letter-spacing: -1px;
  }
  .footer__copy i {display: none;}
  .footer__copy a {
    position: absolute;
    display: inline-block;
    left: 0; top: -20px;
  }
}