.about {
  position: relative;
}
.about__header {
  @include __subgradient(#88d9f0, #94e5ab);
}

.about__body {
  padding: 100px 0 110px;
  background: #f8f9f9; /* Old browsers */
  background: -moz-linear-gradient(top, #f8f9f9 0%, #e7f4f8 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #f8f9f9 0%, #e7f4f8 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #f8f9f9 0%, #e7f4f8 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f8f9f9', endColorstr='#e7f4f8',GradientType=0 ); /* IE6-9 */
}

.about__description {
  position: relative;
  margin: 0 auto;
  max-width: $sub-width;
  text-align: center;
}

.about__description-title {
}
.about__description-txt {
  padding: 40px 0 30px;
  color: #2a2932;
  line-height: 1.5;
  font-size: $fs18;
  font-weight: 300;
  @extend %word-break;
  margin: 0 auto;
  max-width: 770px;
}
.about__description:hover .about__description-title img {
  @extend %ani-rubber;
}
.about__playstudio {
  position: relative;
  margin: 0 auto;
  max-width: $sub-width;
  text-align: center;
  @extend %clearfix;
}
.about__playstudio-character {
  text-align: center;
  font-size: 0;
  img {
    max-width: 100%;
  }
}
$about-block-height: 550px;
%about__playstudio {
  position: relative;
  float: left;
  width: 50%;
  height: $about-block-height;
}

.about__playstudio-value {
  @extend %about__playstudio;
  background-color: #4b4c56;
  color: #fff;
  .about__playstudio-value-tb {
    display: table;
    width: 100%;
    height: 100%;
  }
  .table-cell {
    display: table-cell;
    vertical-align: middle;
  }
  .about__playstudio-value-title {
    font-size: $fs40;
    line-height: 1.3;
    padding-bottom: 0.5em;
  }
  .about__playstudio-value-txt {
    font-size: $fs18;
    line-height: 1.5;
    font-weight: 200;
    @extend %word-break;
    padding: 0 30px;
  }
}

.valueslider {
  @extend %about__playstudio;
  background-color: #ffffff;
}
.valueslider .slides {
  height: $about-block-height;
}
.valueslider .slides li {
  position: relative;
  display: none;
  text-align: center;
  padding-top: 40px;
}

.valueslider .flex-direction-nav {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 10;
}
.valueslider .flex-direction-nav .flex-disabled {
  display: none !important;
}
.valueslider .flex-direction-nav .flex-prev,
.valueslider .flex-direction-nav .flex-next {
  display: block;
  position: absolute;
  bottom: 37px;
  width: 70px;
  height: 70px;
  text-indent: -9999px;
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: 100%;
}
.valueslider .flex-direction-nav .flex-prev {
  left: 20px;
  background-image: url(../img/about/prev.png);
}
.valueslider .flex-direction-nav .flex-prev:hover {
  background-image: url(../img/about/prev_h.png);
}
.valueslider .flex-direction-nav .flex-next {
  right: 20px;
  background-image: url(../img/about/next.png);
}
.valueslider .flex-direction-nav .flex-next:hover {
  background-image: url(../img/about/next_h.png);
}
.valueslider .valueslider__counter {
  display: block;
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 58px;
  text-align: center;
  color: #909090;
  font-size: $fs24;
}

.valueslider__value {
  line-height: 1.5;
  color: #3e3e3e;
}
.valueslider__value-symbol {
  display: table;
  width: 100%;
  height: 245px;
  text-align: center;
  > span {
    display: table-cell;
    vertical-align: middle;
  }
}

.valueslider__value-title {
  font-size: $fs40;
  padding-bottom: 0.2em;
  overflow: hidden;
}
.valueslider__value-txt {
  overflow: hidden;
  color: #555555;
  font-size: $fs18;
}

//  -- add new contents

.about__benefit {
  background-color: #f8f9f9;
  position: relative;
  padding: 100px 0 100px;
  text-align: center;
  &:hover .about__benefit-title img {
    @extend %ani-rubber;
  }
  .about__benefit-txt {
    font-size: $fs18;
    padding: 40px 0 70px;
  }
  .benefit-list {
    &:after {
      content: '';
      clear: both;
      display: block;
    }
    $li-width: 560px;
    $li-height: 150px;
    width: $li-width * 2;
    margin: 0 auto;
    text-align: left;
    li {
      float: left;
      position: relative;
      width: $li-width - 120px;
      height: $li-height;
      padding-left: 120px;
      h4 {
        font-weight: normal;
        font-size: $fs24;
        height: 48px;
      }
      p {
        color: #666;
        font-size: $fs16;
        line-height: 1.6;
      }
    }
    .benefit__icon {
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: 90px;
      height: 90px;
      background-repeat: no-repeat;
      background-position: 50% 50%;
    }
    @for $i from 1 to 20 {
      .benefit__icon--#{$i} {
        background-image: url(../img/about/ico-benefit-#{$i}.png);
      }
    }
  }
}

@media (max-width: 1100px) and (min-width: 761px) {
  .about__body {
    padding: 100px 30px 110px;
  }
  .about__playstudio-value,
  .valueslider {
    height: 400px;
  }
  .valueslider .slides {
    height: 400px;
  }

  .about__playstudio-value .table-cell {
    padding: 0 10px;
  }
  .about__playstudio-value .about__playstudio-value-title {
    font-size: 25px;
  }
  .about__playstudio-value .about__playstudio-value-txt {
    font-size: 16px;
  }
  .valueslider .slides li {
    padding-top: 40px;
  }
  .valueslider__value-symbol {
    display: block;
    height: 150px;
    span {
      display: block;
      height: 100%;
    }
    img {
      max-height: 80%;
    }
  }
  .valueslider__value-title {
    font-size: 25px;
  }
  .valueslider__value-txt {
    font-size: 16px;
  }
  .valueslider .valueslider__counter {
    font-size: 18px;
    bottom: 20px;
  }
  .valueslider .flex-direction-nav .flex-next,
  .valueslider .flex-direction-nav .flex-prev {
    width: 40px;
    height: 40px;
    bottom: 14px;
  }

  .about__benefit {
    padding: 70px 30px 70px;
    .about__benefit-txt {
      padding: 40px 0 70px;
      font-size: $fs14;
    }
    .benefit-list {
      width: 100%;
      margin: 0 auto;
      text-align: left;
      li {
        box-sizing: border-box;
        float: left;
        position: relative;
        width: 50%;
        height: 130px;
        padding-left: 90px;
        h4 {
          font-size: $fs18;
          height: 30px;
        }
        p {
          width: 80%;
          font-size: 12px;
          br {
            display: none;
          }
        }
      }
      .benefit__icon {
        width: 70px;
        height: 70px;
        background-size: 70px 70px;
      }
    }
  }
}
@media (max-width: 760px) {
  .about__header .sub__header-character {
    width: 60%;
    margin: 30px auto 0;
    padding-bottom: 30px;
    img {
      margin-right: -14%;
    }
  }
  .about__body {
    padding: 50px 0 50px;
    .about__description-title {
      img {
        height: 21px;
      }
    }
    .about__description-txt {
      padding: 20px 10px 30px;
      font-size: 12px;
      letter-spacing: -1px;
    }
  }
  .about__playstudio {
    padding: 0 15px;
  }
  .about__playstudio-value,
  .valueslider {
    float: none;
    width: 100%;
    height: 300px;
  }
  .valueslider .slides {
    height: 300px;
  }

  .about__playstudio-value .table-cell {
    padding: 0 10px;
  }
  .about__playstudio-value .about__playstudio-value-title {
    font-size: 20px;
  }
  .about__playstudio-value .about__playstudio-value-txt {
    font-size: 12px;
  }
  .valueslider__value-symbol {
    display: block;
    height: 130px;
    span {
      display: block;
      height: 100%;
    }
    img {
      max-height: 80%;
    }
  }
  .valueslider__value-title {
    font-size: 20px;
  }
  .valueslider__value-txt {
    font-size: 12px;
  }
  .valueslider .valueslider__counter {
    font-size: 14px;
    bottom: 10px;
  }
  .valueslider .flex-direction-nav .flex-next,
  .valueslider .flex-direction-nav .flex-prev {
    width: 16px;
    height: 31px;
    bottom: 138px;
  }
  .valueslider .flex-direction-nav .flex-prev {
    left: 18px;
    background-image: url(../img/about/prev_m.png);
    &:hover {
      background-image: url(../img/about/prev_m.png);
    }
  }
  .valueslider .flex-direction-nav .flex-next {
    right: 18px;
    background-image: url(../img/about/next_m.png);
    &:hover {
      background-image: url(../img/about/next_m.png);
    }
  }
  .about__benefit {
    padding: 60px 0px 20px;
    .about__benefit-title img {
      height: 21px;
    }
    .about__benefit-txt {
      padding: 20px 0 45px;
      font-size: 12px;
    }
    .benefit-list {
      width: 100%;
      margin: 0 auto;
      text-align: left;
      li {
        position: relative;
        width: 100%;
        height: 200px;
        text-align: center;
        padding-left: 0;
        h4 {
          font-size: $fs16;
          height: 30px;
        }
        p {
          margin: 0 auto;
          width: 90%;
          font-size: 12px;
        }
      }
      .benefit__icon {
        position: static;
        margin: 0 auto 20px;
        width: 70px;
        height: 70px;
        background-size: 70px 70px;
      }
    }
  }
}
