@mixin __opacity($alpha) {
  $ieAlpha: $alpha * 100;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=#{$ieAlpha});
  opacity: $alpha;
}

@mixin __centercal($width, $height) {
  width: $width;
  height: $height;
  left: 50%;
  top: 50%;
  margin-left: -($width / 2) ;
  margin-top: -($height / 2) ;
}

@mixin __btnshine($start, $end, $duration) {
  transform: translate3d(0,0,0);
  overflow: hidden;
  &:after, &:before {
    content: "";
    display: block;
    position: absolute;
    left: $start;
    top: 0;
    height: 100%;
    width: 10%;
    background-color: #fff;
    transform: skew(-20deg);
    @include __opacity(.2);
    
  }
  &:before {
    width: 30%;
    @include __opacity(.3);
  }
  &:hover:after, &:hover:before {
    transition: left $duration $easeinout;
    left: $end;
  }
  &:hover:after {
    transition-delay: .1s;
  }
}

@mixin __subgradient($startColor, $endColor) {
  
  background: $startColor; /* Old browsers */
  background: -moz-linear-gradient(-45deg, $startColor 1%, $endColor 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, $startColor 1%,$endColor 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, $startColor 1%,$endColor 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#{$startColor}', endColorstr='#{$endColor}',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

$fam_height: 50px;
$container-width: auto;
$easeinout: cubic-bezier(0.4, 0.0, 0.2, 1);
$easeinout2: cubic-bezier(0.4, 0.0, 0.6, 1);
$easeout: cubic-bezier(0.0, 0.0, 0.2, 1);
$easein: cubic-bezier(0.4, 0.0, 1, 1);

%clearfix {
  *zoom: 1;
  &:after {
    clear: both;
    content: '';
    display: table;
  }
}

%ani-rubber {
  animation-iteration-count: 1;
  animation-duration: 1s;
  animation-fill-mode: both;
  animation-name: rubberBand;
  transform-origin: 50% 100%;
}
%word-break {
  word-break: keep-all;
  word-wrap: break-word;
}

%replace-box {
  display: block;
  position: absolute;
  text-indent: -9999px;
  left: 0;
  top: 0; 
  width: 100%;
  height: 100%;
}

%hover-opa {
  @extend %replace-box;  
  &:after,&:before {
    @extend %replace-box;
    content: "";
    transition: opacity .25s $easeout;
  }
  &:before {
    @include __opacity(1);
  }
  &:after {
    @include __opacity(0);
  }
  &:hover:after {
    @include __opacity(1);
  }
}

%poa-obj {
  position: absolute;
  display: block;
  background-repeat: no-repeat;
  background-position: 0 0;
  text-indent: -99999px;
}

