.text--rollup {
  overflow: hidden;
  &> span {
    display: inline-block;
    transform: translate(0, 100%);
    
  }
}
.active--ani .text--rollup {
  &> span {
    transition: transform .4s $easeout .2s;
    transform: translate(0,0);
  }
}

@for $i from 1 through 5 {
  .active--ani .text--rollup:nth-child(#{$i}) span{
    transition-delay: .4s * $i;
  }
}
.particles {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 3;
  transition: opacity 1s;
  @include __opacity(0);
}
.particles.loaded {
  @include __opacity(1);
}

.contents {
  @extend %clearfix;
  position: relative;
  width: $container-width;
  margin: 0 auto;
  overflow: hidden;
}

