@import 'hamtl';
@import 'hambtn';
@import 'hamnav';
@import 'hamsns';

.nav {
    position: fixed; 
    right: 0;
    top: 0;
    width: 0;
    height: 0;
    z-index: 100;
    overflow: hidden;
}
.nav.nav--open {
    width: 100%;
    height: 100%;
    overflow: visible;
}

.nav__bg {
    position: fixed;
    right: -25%;
    top: -25%;
    background-color: #151515;
    width: 0%;
    height: 0%;
    border-radius: 50% 0 0 50%;
    transition: width .375s $easein, height .375s $easein;
   // @include __opacity(0.9);
}

.nav--open .nav__bg {
    width: 165%;
    height: 220%;
}

.nav__body {
    position: absolute;
    @include __centercal(1000px, 868px);
    @include __opacity(0);
    transition: opacity .2s;

}
.nav--open .nav__body {
    @include __opacity(1);
}

.nav__body .familysite__links {
    z-index: 5;
    border-color: #272727;
    color: #a8a8a8;
    background-color: #272727;
    &:after {
        background-image: url(../img/arw_u2.png);
    }
    & .familysite__list a {
        color: #B1B1B1; 
    }
}

.nav__body .familysite {
    height: $fam_height;
    position: relative;
    & .familysite__links {
        position: absolute;
        left: 50%;
        bottom: 0;
        margin-left: -110px;
    }
    @include __opacity(0);
}
.nav--open .familysite {
    @include __opacity(1);
    transition: opacity .4s 1.2s;
}

@media (max-width: 1100px) and (min-width: 761px) {
    .nav.nav--open {
        overflow-y: scroll;
        overflow-x: hidden;
    }
    .nav__body {
        position: relative;
        width: auto;
        height: auto;
        left: auto;
        top: auto;
        margin-left: auto;
        margin-top: auto;
        padding: 100px 0 100px;
    }
    .nav__btn {
        width: $ham-aw * 2 + 50px;
        margin: 0 auto 20px;
        text-align: center;
    }
    .nav__btn a {
        margin:0 10px 20px;
    }
    .nav__sns {
        margin: 40px 0 20px;
    }
}
@media (max-width: 760px) {
    .nav__body {
        position: relative;
        width: auto;
        height: auto;
        left: auto;
        top: auto;
        margin-left: auto;
        margin-top: auto;
        padding: 0 0 50px;
    }
    .nav__title {
        padding-top: 50px;
        padding-bottom: 15px;
    }
    .nav__title-t span {
        height: 50px;
        background-size: auto 100%;
    }
    .nav__title-s span {
        height: 15px;
        background-size: auto 100%;
    }
    
    $ham-aw : 116px * .9;
    $ham-ah : 160px * .9;
    .nav__btn {
        width: $ham-aw * 2 + 20px;
        margin: 0 auto;
        text-align: center;
    }
    
    .nav__btn a {
        width: $ham-aw;
        height: $ham-ah;
        margin: 5px;
        &:after {
            background-size: auto 100%;
            height: 25px;
            bottom: -30px;
        }
        &:hover:after {
            bottom: -15px;
        }
        &:hover .hover--opacity{
            top:-15px;
        }
    }
    .nav .familysite {
        display: none;
    }
    .nav__sns {
        margin-top: 25px;
        height: 40px;
    }
    .nav__sns a {
        width: 40px;
        height: 40px;   
    }
}