.home__links {
    position: relative;
    text-align: center;
    font-size: 0;
    margin: 80px auto 70px;
    z-index: 5;
}
.home__links a {
   display: inline-block;
   position: relative;
   width: 150px;
   height: 250px;
   text-indent: -99999px;
   margin: 0 5px;
   span {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-position: 0 0;
        background-repeat: no-repeat;
        background-size: 100%;
        transition: margin .25s $easeinout, transform 1s $easeinout;
   }
}

.home__links a:hover span {
    margin-top: -15px;
    transform: rotateY(360deg);
}

.home__links-matgo span {
    background-image: url(../img/main/btn_g_1.png);
}
.home__links-poker span {
    background-image: url(../img/main/btn_g_2.png);
}
.home__links-sutda span {
    background-image: url(../img/main/btn_g_3.png);
}
.home__links-blackjack span {
    background-image: url(../img/main/btn_g_4.png);
}
.home__links-slot span {
    background-image: url(../img/main/btn_g_5.png);
