
.visual__earth {
    position: relative;
    width: 1164px;
    height: 1041px;
    margin:0 auto;
    z-index: 4;
    .object {
        transition: opacity .5s $easeout, margin .4s $easeout
    }
}


.object {
    position: absolute;
    display: block;
    background-repeat: no-repeat;
    background-position: 0 0;
}

.obj-earth {
    animation-name: earth-rotate;
    animation-iteration-count: infinite;
    animation-duration: 240s;
    animation-fill-mode: both;
    animation-timing-function: linear;
    @include __centercal(789px, 789px);
    background-image: url(../img/main/main_visual/earth.png);
    margin-left: -384.5px;
    margin-top: -269.5px;
    overflow: hidden;
    .obj-earth-s1 {
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        animation-name: earth-rotate;
        animation-direction: reverse;
        animation-iteration-count: infinite;
        animation-duration: 240s;
        animation-fill-mode: both;
        animation-timing-function: linear;
        background-image: url(../img/main/main_visual/earth_s1.png);
        background-position: 0 0;
    }
}
.obj-sutdah {
    @include __opacity(0);
    @include __centercal(165px, 239px);
    background-image: url(../img/main/main_visual/sutda_h.png);
}
.obj-sutda {
    @include __opacity(0);
    @include __centercal(254px, 154px);
    background-image: url(../img/main/main_visual/sutda.png);
}
.obj-newmatgo {
    @include __opacity(0);
    @include __centercal(311px, 332px);
    background-image: url(../img/main/main_visual/newmatgo.png);
}
.obj-slot {
    @include __opacity(0);
    @include __centercal(250px, 336px);
    background-image: url(../img/main/main_visual/slot.png);
}
.obj-poker {
    @include __opacity(0);
    @include __centercal(417px, 391px);
    background-image: url(../img/main/main_visual/poker.png);
}
.obj-building {
    @include __opacity(0);
    @include __centercal(255px, 251px);
    background-image: url(../img/main/main_visual/building.png);
}
.obj-king {
    @include __opacity(0);
    @include __centercal(338px, 412px);
    background-image: url(../img/main/main_visual/king.png);
    .obj-king_arm {
        width: 102px;
        height: 156px;
        background: url(../img/main/main_visual/king_arm.png);
        left: -50px;
        top: 105px;
        transform-origin: 65% 80%;
        animation-name: king-arm;
        animation-iteration-count: infinite;
        animation-duration: 1s;
        animation-fill-mode: both;
        animation-timing-function: linear;
    }
}
.visual__earth  {
    .object {
        @include __opacity(1);
    }
    .obj-sutdah {
        margin-left: -534.5px;
        margin-top: 30.5px;
    }
    .obj-sutda {
        margin-left: -583px;
        margin-top: 165px;
    }
    .obj-newmatgo {
        margin-left: -549.5px;
        margin-top: -297px;
    }
    .obj-slot {
        margin-left: 333px;
        margin-top: -5px;
    }
    .obj-poker {
        margin-left: 161.5px;
        margin-top: -381.5px;
    }
    .obj-building {
        margin-left: -354.5px;
        margin-top: -406.5px;
    }
    .obj-king {
        margin-left: -116px;
        margin-top: -522px;
    }
}

@for $i from 1 through 8 {
    .visual__earth .object:nth-child(#{$i}) {
       transition-delay: $i * .1s;
    }
}


