.visual__card .object {
    @include __opacity(0);
    transition: opacity .25s $easeout, transform .25s $easeout;
}

.visual__card.active--ani .object {
    transition: opacity .25s $easeout, transform .45s $easeout;
    transform: translate(0,0) rotateZ(0);
    @include __opacity(1);
}

@for $i from 1 through 8 {
    .visual__card.active--ani .obj-card#{$i} {
       transition-delay: $i * .1s;
    }
}

@for $i from 1 through 4 {
    .visual__card.active--ani .obj-chip#{$i} {
       transition-delay: 1s + ($i * .1s);
    }
}
 