
// _base -- $fam_height;
.familysite__links {
    position: relative;
    margin: 0 auto;
    width: 220px;
    $color: #666;
    border: 1px solid #d1d1d1;
    font-size: 18px;
    text-align: left;
    & .familysite-link-family {
        cursor: pointer;
        padding: 0 40px 0 20px;
        display: block;
        height: $fam_height;
        line-height: $fam_height;
        color: $color;
    }
    &:after {
        content: "";
        display: block;
        position: absolute;
        text-indent: -9999px;
        right: 14px;
        bottom: 21px;
        width: 21px;
        height: 11px;
        background: url(../img/arw_u.png) no-repeat ;
        transition: transform .25s ease;
    }
    &:hover:after {
       transform: rotateZ(180deg);
    }
    &:hover .familysite__list {
       height: $fam_height * .7 * 3;
    }
}
.familysite__list {
    height: 0;
    transition: height .25s ease;
    overflow: hidden;
    & a {
        display: block;
        padding: 0 20px;
        height: $fam_height * .7;
        line-height: $fam_height * .7;
        color: #353535;
        font-size: 16px;
    }
}