.nav__title {
    padding-bottom: 80px;
}
.nav__title-t {
    position: relative;
    overflow: hidden;
    & span {
        display: block;
        height: 121px;
        background: url(../img/ham_play.png) no-repeat 50% 0;
        text-indent: -9999px;
        transform: translate(0,100%);
        transition: transform .25s $easeout .4s;
    }
}
.nav__title-s {
    position: relative;
    overflow: hidden;
    & span {
        display: block;
        height: 31px;
        background: url(../img/ham_world.png) no-repeat 50% 0;
        text-indent: -9999px;
        transform: translate(0,100%);
        transition: transform .25s $easeout .6s;
    }
}

.nav--open .nav__title-t span, .nav--open .nav__title-s span{
    transform: translate(0,0);
}