.nav__out {
  position: absolute;
  right: 30px;
  top: 52px;
  font-size: 0;
  z-index: 50;
  a {
    position: relative;
    display: inline-block;
    height: 44px;
    padding: 0 30px;
    line-height: 44px;
    font-size: 20px;
    color: #000;
    text-decoration: none;
  }
  .on {
    font-weight: bold;
  }
  .underline {
    display: block;
    position: absolute;
    left: 30px; 
    right: 30px;
    top: 43px;
  }
}
.nav__out .underline:after {
  content: "";
  clear: both;
  display: block;
  position: absolute;
  right: 0px;
  left: auto;
  bottom: 8px;
  width: 0;
  height: 2px;
  background: #111111;
  transition: width .25s, transform .4s
}
.nav__out a:hover .underline:after, .nav__out .on .underline:after {
  width: 100%;
  right: auto;
  left: 0px
}
@media (max-width: 1100px) {
  .nav__out {
    display: none;
  }
}