.sub__header-wrap {
  position: relative;
  margin: 0 auto;
  padding: 0 30px;
  max-width : $sub-width;
  height: 600px;
}
.sub__particles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: opacity 1s $easeinout;
  @include __opacity(0);
}
.sub__particles.loaded {
  @include __opacity(1);
}
.sub__header .bg--replace.add--bg {
  background: url(../img/top_star.png) no-repeat 50% 0;
}
.sub__header-title {
  position: relative;
  color: #000000;
  font-size: $fs72;
  font-weight: normal;
  padding: 175px 0 20px;
  line-height: 1.4;
  z-index: 5;
  font-weight: 300;
}
.sub__header-txt {
  position: relative;
  color: #000000;
  font-weight: 300;
  font-size: $fs18;
  line-height: 1.4;
  z-index: 5;
}

.sub__header-character {
  position: absolute;
  right: 0; 
  top: 0;
  height: 100%;
  display: table;
  span {
    display: table-cell;
    vertical-align: middle;
  }
}

// delay
.active--ani .sub__header-txt.text--rollup span {
  transition-delay: 1.2s;
}
@media (max-width: 1100px) and (min-width: 761px) {
  .sub__header-wrap {
    height: 400px;
  }
  .sub__header-title {
    padding: 130px 0 10px;
    font-size: 40px;
    line-height: 1.3;
  }
  .sub__header-txt {
    font-size: 14px;
    letter-spacing: -1px;
  }
  .sub__header-character {
    right: 30px;
    max-width: 300px;
    img {
      max-width: 100%;
    }
  }
}
@media (max-width: 760px) {
  .sub__header-wrap {
    padding: 0;
    height: auto;
    text-align: center;
  }
  .sub__header-title {
    padding: 100px 0 10px;
    font-size: 30px;
    line-height: 1.2;
    text-align: center;
  }
  .sub__header-txt {
    font-size: 14px;
    letter-spacing: -1px;
  }
  .sub__header-character {
    position: relative;
    display: block;
    max-width: 300px;
    span {
      position: relative;
      display: block;
    }
    img {
      max-width: 100%;
    }
  }
}