.careers__header {
  @include __subgradient(#a6c1ee, #fbc2eb);
  &:after {
    content: '';
    display: block;
    position: absolute;
    left: 50%;
    margin-left: 545.5px;
    top: -135px;
    width: 225px;
    height: 225px;
    background: url(../img/careers/character_2.png) no-repeat 0 0;
  }
}
.careers__header .sub__header-wrap {
}
.careers__header .sub__header-character {
  display: block;
  max-height: 537px;
  overflow: hidden;
  right: -241px;
  top: 63px;
  img {
    position: relative;
    z-index: 3;
  }
}

.careers__people {
  position: relative;
  z-index: 5;
  padding: 100px 0 100px;
}
.careers__people:hover .careers__people-title img {
  @extend %ani-rubber;
}
.careers__people-title {
  text-align: center;
  padding-bottom: 50px;
}

.careers__people-list {
  @extend %clearfix;
  font-size: 0;
  max-width: 1150px;
  margin: 0 auto;
  padding: 0 70px;
  text-align: center;
}

.careers__people-cell {
  display: inline-block;
  width: 200px;
  height: 320px;
  padding: 0 15px;
}

.careers__people-cell-btn {
  display: block;
  width: 200px;
  height: 200px;
  position: relative;
  border-radius: 50%;
}
.careers__people-cell-btn-thumb-tb {
  position: absolute;
  display: table;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  .thumb-cell {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
  }
  overflow: hidden;
  img {
    max-width: 200px;
  }
}
.careers__people-cell-btn-border {
  z-index: 1;
  box-sizing: border-box;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 1px solid #e9e9e9;
  transition: border 0.25s $easeinout;
}
.careers__people-cell-btn:hover .careers__people-cell-btn-border,
.open .careers__people-cell-btn-border {
  border: #e08af8 solid 5px;
}
.careers__people-cell-job {
  text-align: center;
  padding: 20px 0 10px;
  color: #666666;
  font-size: $fs16;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.careers__people-cell-name {
  text-align: center;
  color: #000000;
  font-size: $fs20;
}
.careers__people-cell .careers__people-detail {
  display: none;
}
.careers__people-detail {
  position: absolute;
  display: block;
  width: 2560px;
  left: 50%;
  bottom: 0;
  margin-left: -1280px;
  height: 100%;
  overflow: hidden;
}
.careers__people-wrap {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}
.careers__people-detail-body {
  box-sizing: border-box;
  padding-left: 350px;
  max-width: 1100px;
  margin: 0 auto;
  font-size: $fs16;
  position: relative;
  z-index: 5;
  text-align: left;
  color: #000000;
  line-height: 1.6;
  @extend %word-break;
}
.careers__people-detail-thumb {
  position: absolute;
  left: 20px;
  top: 50%;
  margin-top: -140px;
  width: 280px;
  height: 280px;
  img {
    max-width: 100%;
  }
}
.careers__people-detail-title {
  font-size: $fs16;
  padding: 0px 0 20px;
  vertical-align: bottom;
  em {
    font-size: $fs36;
  }
  span {
    padding-left: 20px;
  }
}
.careers__people-detail-desc {
  font-size: $fs16;
  font-weight: 300;
  padding: 0px 0 25px;
  @include __opacity(0.9);
  @extend %word-break;
}
.careers__people-detail-btn {
  margin-left: -10px;
  display: block;
  text-indent: -99999px;
  width: 328px;
  height: 96px;
  background: url(../img/careers/btn_play.png);
  border-radius: 96px;
  @include __btnshine(-35%, 125%, 1s);
}

.careers__team {
  background-color: #f8f9f9;
  padding: 100px 0 100px;
}
.careers__team:hover .careers__team-title img {
  @extend %ani-rubber;
}
.careers__team-title {
  position: relative;
  z-index: 5;
  text-align: center;
  padding: 0 0 50px;
}

.careers__team-list {
  position: relative;
  z-index: 5;
  @extend %clearfix;
  font-size: 0;
  max-width: 350px * 3;
  margin: 0 auto;
  padding: 0 5px;
}

.careers__team-cell {
  display: inline-block;
  width: 350px;
  height: 400px;
  line-height: 1.5;
  text-align: center;
  vertical-align: top;
}

.careers__team-cell-symbol {
  display: table;
  width: 100%;
  height: 140px;
  span {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
  }
}
.careers__team-cell-part {
  color: #666666;
  font-size: $fs18;
}

.careers__team-cell-title {
  color: #000;
  font-size: $fs24;
  padding-bottom: 20px;
}
.careers__team-cell-desc {
  color: #666666;
  font-size: 16px;
  padding: 0 2em;
  @extend %word-break;
  text-align: left;
}
.careers__apply {
  height: 370px;
  background-color: #292929;
  text-align: center;
}
.careers__apply-title {
  font-size: $fs46;
  color: #fff;
  font-weight: 300;
  padding: 60px 0 20px;
}
.careers__apply-desc {
  line-height: 1.5;
  color: #fff;
  font-size: 20px;
  @include __opacity(0.5);
  padding-bottom: 40px;
}

.careers__apply-btn {
  position: relative;
  z-index: 5;
  display: block;
  width: 338px;
  height: 92px;
  text-indent: -99999px;
  margin: 0 auto;
  background: url(../img/careers/btn_apply.png) no-repeat 0 0;
  background-size: 100%;
  border-radius: 46px;
  @include __btnshine(-35%, 125%, 1s);
}

.careers__people-wrap {
  transition: transform $open-delay $easeinout, background 0.5s $easeinout;
  background-color: transparent;
  display: flex;
  align-items: center;
  // justify-content: center;
}
.careers__people-detail-thumb,
.careers__people-detail-title,
.careers__people-detail-desc {
  transition: opacity 0.4s $easeout, transform 0.4s $easeout;
  @include __opacity(0);
  transform: translate(40px, 0);
}
.state--current {
  .careers__people-wrap {
    z-index: 11;
    transform: translate(0, 0);
    background-color: #ececec;
  }
  .careers__people-detail-thumb,
  .careers__people-detail-title,
  .careers__people-detail-desc {
    @include __opacity(1);
    transform: translate(0, 0);
  }
  .careers__people-detail-thumb {
    transition-delay: 0.6s;
  }
  .careers__people-detail-title {
    transition-delay: 0.8s;
  }
  .careers__people-detail-desc {
    transition-delay: 1s;
  }
}
.state--before {
  .careers__people-wrap {
    z-index: 10;
    transform: translate(0, 0);
    background-color: #ececec;
  }
  .careers__people-detail-thumb,
  .careers__people-detail-title,
  .careers__people-detail-desc {
    @include __opacity(1);
    transform: translate(-40px, 0);
  }
  .careers__people-detail-thumb {
    transition-delay: 0.2s;
  }
  .careers__people-detail-title {
    transition-delay: 0.4s;
  }
  .careers__people-detail-desc {
    transition-delay: 6s;
  }
}
.careers__team .valueslider {
  display: none;
}
@media (max-width: 1100px) and (min-width: 761px) {
  .careers__header .sub__header-character {
    right: -30px;
    min-width: 400px;
    max-height: 315px;
    top: 85px;
  }
  .careers__people-list {
    padding: 0;
  }
  .careers__people-detail-thumb {
    display: none;
  }
  .careers__people-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .careers__people-detail-body {
    padding: 0 30px;
    max-width: 700px;
    height: auto;
  }
  .careers__people-detail-title {
    padding: 0px 0 15px;
  }
  .careers__team-list {
    text-align: center;
  }
  .careers__team-cell {
    width: 250px;
  }
  .careers__team-cell-desc {
    font-size: 14px;
    padding: 0 10px;
  }
  .careers__apply {
    height: auto;
    padding-bottom: 30px;
  }
  .careers__apply-title {
    font-size: 30px;
    padding: 30px 0 10px;
  }
  .careers__apply-desc {
    font-size: 16px;
    padding-bottom: 20px;
  }
  .careers__apply-btn {
    width: 220px;
    height: 61px;
  }
}
@media (max-width: 760px) {
  .careers__header .sub__header-title .text--rollup:nth-child(1),
  .careers__header .sub__header-title .text--rollup:nth-child(2) {
    display: inline-block;
  }
  .careers__header .sub__header-title .text--rollup:nth-child(3) {
    clear: both;
  }
  .careers__header .sub__header-character {
    right: -7%;
    top: auto;
    margin: 0 auto;
    height: 230px;
  }
  .careers__people {
    padding: 50px 0 50px;
  }
  .careers__people-title {
    padding-bottom: 20px;
    img {
      height: 21px;
    }
  }
  .careers__people-list {
    padding: 0;
  }
  .careers__people-cell {
    box-sizing: border-box;
    width: 33%;
    height: 165px;
    padding: 0;
  }
  .open .careers__people-cell-btn-border,
  .careers__people-cell-btn:hover .careers__people-cell-btn-border {
    border-width: 3px;
  }
  .careers__people-cell-btn {
    overflow: hidden;
    width: 100px;
    height: 100px;
    margin: 0 auto;
  }
  .careers__people-cell-btn-thumb-tb {
    overflow: hidden;
    img {
      max-width: 100px;
    }
  }
  .careers__people-cell-job {
    padding: 10px 0 5px;
    font-size: 11px;
  }
  .careers__people-cell-name {
    font-size: 13px;
  }

  //open
  .careers__people .open-block.active--ani {
    height: 320px;
    margin-bottom: 15px;
  }
  .careers__people-detail {
    width: 100%;
    left: 0;
    top: 0;
    margin-left: 0;
  }
  .careers__people-detail-thumb {
    display: none;
  }
  .careers__people-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .careers__people-detail-body {
    padding: 0 15px 0;
    text-align: center;
    height: auto;
  }
  .careers__people-detail-platform {
    padding: 5px 0 0;
    font-size: 11px;
  }
  .careers__people-detail-title {
    padding: 30px 0 10px;
    font-size: 18px;
  }
  .careers__people-detail-title em {
    display: block;
    font-size: 18px;
  }
  .careers__people-detail-title span {
    font-size: 11px;
    display: block;
    position: absolute;
    left: 0;
    top: 10px;
    width: 100%;
    padding: 0;
    color: #7a7a7a;
    text-align: center;
  }
  .careers__people-detail-desc {
    font-size: 11px;
    line-height: 1.4;
    padding: 5px 0 15px;
  }
  .careers__team {
    padding: 50px 0 50px;
  }
  .careers__team-title {
    padding-bottom: 20px;
    img {
      height: 21px;
    }
  }
  .careers__team-list {
    display: none;
  }
  .careers__team .valueslider {
    display: block;
    background-color: #f8f9f9;
    z-index: 10;
  }
  .careers__team-cell-symbol {
    height: 110px;
  }
  .careers__team-cell-title {
    font-size: 20px;
    padding-bottom: 5px;
  }
  .careers__team-cell-part {
    font-size: 11px;
  }
  .careers__team-cell-desc {
    padding: 10px 15px 0;
    font-size: 12px;
    text-align: center;
  }
  .careers__apply {
    height: auto;
    padding-bottom: 30px;
  }
  .careers__apply-title {
    font-size: 23px;
    padding: 30px 0 10px;
  }
  .careers__apply-desc {
    font-size: 12px;
    padding-bottom: 20px;
  }
  .careers__apply-btn {
    width: 170px;
    height: 47px;
  }
}
