.bottomlinks {
    position: relative;
    height: 500px;
    background-color: #292929;
    z-index: 5;
}
.bottomlink__about, .bottomlink__careers {
    width: 50%;
    float: left;
    height: 500px;
    box-sizing: border-box;
    a {
        box-sizing: border-box;
        position: relative;
        display: block;
        height: 500px;
        padding: 60px;
        text-decoration: none;
    }
    .bottomlinks__link-tl {
        position: relative;
        z-index: 5;
        display: block;
        text-indent: -9999px;
        margin-bottom: 30px;
        background-repeat: no-repeat;
        background-position: 0 0;
        background-size: auto 100%;
    }
    .bottomlinks__link-subtl {
        position: relative;
        font-size: $fs30;
        line-height: 1.3;
        color: #fff;
        z-index: 5;
    }
    :hover .bottomlinks__link-tl {
        @extend %ani-rubber;
    }
}
$bottomlink-mid-margin : 50px;
.bottomlink__about {
    padding-right: $bottomlink-mid-margin;
    .bottomlinks__link-tl {
        width: 220px;
        height: 38px;
        background-image: url(../img/main/bt_title1.png);
    }
    .bottomlinks__about__link-character {
        @extend %poa-obj;
        width: 332px;
        $height: 360px;
        height : $height;
        top: 50%;
        margin-top: -($height / 2);
        right: 60px;
        background: url(../img/main/bt_cha1.png) no-repeat 0 0;
        background-size: 100%;
    }
}
.bottomlink__careers {
    padding-left: $bottomlink-mid-margin; 
    .bottomlinks__link-tl {
        width: 220px;
        height: 38px;
        background-image: url(../img/main/bt_title2.png);
    }
    .bottomlinks__careers__link-character {
        @extend %poa-obj;
        width: 344px;
        $height: 366px;
        height : $height;
        top: 50%;
        margin-top: -($height / 2);
        right: 60px;
        background: url(../img/main/bt_cha2.png) no-repeat 0 0;
        background-size: 100%;
    }
}
.bottomlink__careers a {
    background-color: #353535;
    &:after {
        content: "";
        display: block;
        position: absolute;
        left: -$bottomlink-mid-margin;
        top: 0;
        width: $bottomlink-mid-margin*2;
        height: 100%;
        background-color: #353535;
        transform: skew(-10deg);

    }
}

@media (max-width:1300px) {
    .bottomlink__about .bottomlinks__about__link-character,
    .bottomlink__careers .bottomlinks__careers__link-character {
        transform: scale(.8);
        transform-origin: 100% 100%;
        right: 10px;
    }
    .bottomlink__about .bottomlinks__link-subtl, .bottomlink__careers .bottomlinks__link-subtl {
        font-size: $fs24;
    }
}

@media (max-width: 1100px) and (min-width: 761px) {
    .bottomlink__about .bottomlinks__link-tl, .bottomlink__careers .bottomlinks__link-tl {
        height: 25px;
        margin-bottom: 15px;
    }
    
    .bottomlink__about .bottomlinks__link-subtl, .bottomlink__careers .bottomlinks__link-subtl {
        font-size: 20px;
    }
}

@media (max-width: 760px) {
   .bottomlinks {
       height: auto;
   }
   .bottomlink__about, .bottomlink__careers {
       float: none;
       width: 100%;
       padding: 0;
       height: 150px;
   }
   .bottomlink__about a, .bottomlink__careers a {
       padding: 20px;
       height: 150px;
   }
   .bottomlink__about .bottomlinks__link-tl, .bottomlink__careers .bottomlinks__link-tl {
       
       height: 17px;
       margin-bottom: 11px;
   }
   .bottomlink__about .bottomlinks__link-subtl, .bottomlink__careers .bottomlinks__link-subtl {
       font-size: 12px;
   }
   .bottomlink__about .bottomlinks__about__link-character, .bottomlink__careers .bottomlinks__careers__link-character {
        transform-origin: 100% 50%;
        transform: scale(.3);
        right: 10px;
   }
}