@import 'home/title';
@import 'home/visual';
@import 'home/link';
@import 'home/down';

.home {
    padding: 210px 0 0 0;
    min-height: 2200px;
    @include __subgradient(#6cedff, #e0abfc);
}

.home__subtitle {
    position: relative;
    z-index: 5;
    margin: 80px 0 20px;
    text-align: center;
    color: #14648c;
    line-height: 1.5;
    font-size: $fs18;
    strong {
        display: block;
        font-size: $fs40;
        font-weight: normal;
        margin-bottom: 25px;
    }
    > span {
        font-weight: 300;
    }
     
}
@media (max-width: 1100px) and (min-width: 761px) {
    .home {
        padding-top: 200px;
        min-height: auto;
    }
    .home__title {
        position: absolute; 
        left: 50%;
        transform: scale(.8) translateX(-50%);
        transform-origin: 0 0;
    }
    .home__subtitle {
        font-size: 12px;
        margin: 240px 0 20px;
        strong {
            font-size: $fs24;
            margin-bottom: 5px;
        }
    }
    .object-backplanet {
        .object {
            transform: scale(.6);
        }
        .obj-bg-p1 {
            margin-left: -65%;
            margin-top: -64%;
        }
        .obj-bg-p2 {
            margin-left: 23%;
            margin-top: -74%;     
        }
        .obj-bg-p3 {
            margin-left: -99%;
            margin-top: -30%;
        }
        .obj-bg-p4 {
            margin-left: 27%;
            margin-top: -31%;
        }
    }
    .home__visual {
        padding-top: 10px;
        height: 700px;
    }
    .visual__earth {
        transform: scale(.6) translateX(-50%);
        transform-origin: 0 0;
        position: absolute;
        left: 50%;
        top: 550px;
    }

    .visual__card {
        transform: scale(.6) translateX(-50%);
        transform-origin: 0 0;
        position: absolute;
        left: 50%;
        top: 1275px
    }
    .home__links {
        max-width: 815px;
        margin: 70px auto 60px;
    }
    .home__links a {
        width: 107px;
        height: 179px;
    }
    .home__downapps {
        padding-bottom: 70px;
    }
    .home__downapps-playsotre {
        width: 290px;
        height: 77px;
    }
}
@media (max-width: 760px) {
    .home {
        padding-top: 85px;
        min-height: auto;
    }
    .home__title {
        position: absolute; 
        left: 50%;
        transform: scale(.5) translateX(-50%);
        transform-origin: 0 0;
    }
    .home__subtitle {
        font-size: 12px;
        margin: 148px 0 20px;
        strong {
            font-size: $fs16;
            margin-bottom: 5px;
        }
    }
    .object-backplanet {
        .object {
            transform: scale(.2);
        }
        .obj-bg-p1 {
            margin-left: -65%;
            margin-top: -147%;
        }
        .obj-bg-p2 {
            margin-left: -15%;
            margin-top: -204%;     
        }
        .obj-bg-p3 {
            margin-left: -173%;
            margin-top: -200%;
        }
        .obj-bg-p4 {
            margin-left: -36%;
            margin-top: -101%;
        }
    }
    .home__visual {
        padding-top: 10px;
        height: 310px;
    }
    .visual__earth {
        transform: scale(.3) translateX(-50%);
        transform-origin: 0 0;
        position: absolute;
        left: 50%;
        top: 320px;
    }

    .visual__card {
        transform: scale(.3) translateX(-50%);
        transform-origin: 0 0;
        position: absolute;
        left: 50%;
        top: 794px;
    }
    .home__links {
        max-width: 285px;
        margin: 55px auto 40px;
    }
    .home__links a {
        width: 80px;
        height: 133px;
        margin: 0 5px 5px;
    }
    .home__downapps {
        padding-bottom: 30px;
    }
    .home__downapps-playsotre {
        width: 210px;
        height: 55px;
    }
    
}
