.home__title.active--ani span {
    animation-name: rubberBand;
    animation-iteration-count: 1;
    animation-duration: 1s;
    animation-fill-mode: both;
    transform-origin: 50% 100%;
}

@for $i from 1 through 12 {
    .home__title-#{$i} {
       animation-delay: $i * .1s;
    }
