
.nav__hambtn {
    display: none;
    position: fixed;
    width: 80px;
    height: 80px;
    right: 30px;
    top: 30px;
    background: url(../img/ham.png) no-repeat 0 0;
    overflow: hidden;
    z-index: 101;
}

%hambtn-line {
    display: block;
    width: 34px;
    height: 4px;
    background-color: #fff;
    border-radius: 2px;
    position: absolute;
    left: 50%;
    margin-left: -17px;
}
.nav__hambtn-line1 {
    @extend %hambtn-line;
    top: 28px;
    transition: transform .5s $easeinout, top .2s $easeinout .5s;
}

.nav__hambtn-line2 {
    @extend %hambtn-line;
    top: 38px;
    transition: transform .5s $easeinout .2s, margin-left .2s  $easeinout;
}
.nav__hambtn-line3 {
    @extend %hambtn-line;
    top: 48px;
    transition: transform .5s $easeinout .1s, top .2s $easeinout .6s;
}

.nav--open .nav__hambtn-line1 {
    top: 38px;
    transform: rotateZ(675deg);
    transition: transform 1s $easeinout .2s, top .2s $easeinout;
}
.nav--open .nav__hambtn-line2 {
    margin-left: 80px;
    transform: rotateZ(720deg);
}

.nav--open .nav__hambtn-line3 {
  top: 38px;
  transform: rotateZ(765deg);
  transition: transform 1s $easeinout .2s, top .2s $easeinout .1s;
}
@media (max-width: 1100px) and (min-width: 761px) {
    .nav__hambtn {
      display: block;
      right: 20px;
      top: 12px;
      transform: scale(.7);
      transform-origin: 100% 0;
    }
}
@media (max-width: 760px) {
    .nav__hambtn {
      display: block;
      right: 10px;
      top: 10px;
      transform: scale(.5);
      transform-origin: 100% 0;
    }
    
}