$ham-aw : 232px;
$ham-ah : 319px;
.nav__btn {
    @extend %clearfix;
    font-size:0;
    width: $ham-aw * 4;
}

.nav__btn a {
    position: relative;
    display: inline-block;
    width: $ham-aw;
    height: $ham-ah;
    &:after {
        display: block;
        content: "";
        position: absolute;
        width: 100%;
        height: 57px;
        left: 0;
        bottom: -83px;
        background: url(../img/arw.png) no-repeat 50% 50%;
        @include __opacity(0);
        transition: opacity .25s $easeout, bottom .6s cubic-bezier(0.39, 1.29, 0.71, 1.51);
    }
    &:hover:after {
        bottom: -43px;
        @include __opacity(1);
    }
    & .hover--opacity{
        transition: top .25s $easeinout .2s ;
    }
    &:hover .hover--opacity{
        top:-30px;
    }
    @include __opacity(0);
    transition: transform .25s $easeout, opacity .1s;
}

.nav__btn a:nth-child(1) {transform: translate($ham-aw*4, 0);}
.nav__btn a:nth-child(2) {transform: translate($ham-aw*3, 0);}
.nav__btn a:nth-child(3) {transform: translate($ham-aw*2, 0);}
.nav__btn a:nth-child(4) {transform: translate($ham-aw*1, 0);}

.nav--open .nav__btn a:nth-child(1) {transition-delay: .9s;}
.nav--open .nav__btn a:nth-child(2) {transition-delay: .8s;}
.nav--open .nav__btn a:nth-child(3) {transition-delay: .7s;}
.nav--open .nav__btn a:nth-child(4) {transition-delay: .6s;}
.nav--open .nav__btn a {transform:translate(0,0); @include __opacity(1);}

.nav__btn .hover--opacity {
    @extend %hover-opa;
    &:before, &:after {
        background-position: 0 0;
        background-repeat: no-repeat;
        background-size: 100% 100%;
    }
}
.on .hover--opacity:after {
    @include __opacity(1);
}

.nav__btn-home .hover--opacity{
    &:before {
        background-image: url(../img/nav_1.png);
    }
    &:after {
        background-image: url(../img/nav_1_on.png);
    }
}

.nav__btn-about .hover--opacity {
    &:before {
        background-image: url(../img/nav_2.png);
    }
    &:after {
        background-image: url(../img/nav_2_on.png);
    }
}

.nav__btn-product .hover--opacity {
    &:before {
        background-image: url(../img/nav_3.png);
    }
    &:after {
        background-image: url(../img/nav_3_on.png);
    }
}

.nav__btn-careers .hover--opacity {
    &:before {
        background-image: url(../img/nav_4.png);
    }
    &:after {
        background-image: url(../img/nav_4_on.png);
    }
}