.responsive__box {
  cursor: pointer;
}

.open-block {
  position: relative;
  padding-bottom: 1px;
  height: 1px;
  margin-bottom: 0;
  transition: all $open-delay $easeinout;
}

.product__game .open-block.active--ani {
  height: 470px;
  padding-bottom: 0px;
  margin-bottom: 40px;
}

.careers__people .open-block.active--ani {
  height: 400px;
  padding-bottom: 0px;
  margin-bottom: 40px;
  &:last-child {
    margin-bottom: -100px;
  }
}

@media (max-width: 760px) {
  .careers__people .open-block.active--ani {
    &:last-child {
      margin-bottom: -50px;
    }
  }
}
