.nav__sns {
    position: relative;
    margin: 150px 0 20px;
    text-align: center;
    font-size: 0;
    height: 70px;
    overflow: hidden;
}

.nav__sns a {
    display: inline-block;
    width: 70px; 
    height: 70px;
    margin: 0 3px;
    border-radius: 0;
    position: relative;
    @include __btnshine(-50%, 150%, .6s);
    transform: translate3d(0,100%,0);
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 100%;
}
.nav--open .nav__sns a {
    transform: translate3d(0,0,0);
}
.nav--open .nav__sns-f {
    z-index: 3;
    background-image: url(../img/ham_f.png);
    transition: transform .25s $easeinout 1s;
}
.nav--open .nav__sns-y {
    z-index: 2;
    background-image: url(../img/ham_y.png);
    transition: transform .25s $easeinout 1.1s;
}
.nav--open .nav__sns-p {
    z-index: 1;
    background-image: url(../img/ham_p.png);
    transition: transform .25s $easeinout 1.2s;
}