.container__logo-nps {
  display: block;
  position: absolute;
  width: 205px;
  height: 44px;
  left: 40px;
  top: 48px;
  background: url(../img/nps_bi.png) no-repeat 0 0;
  z-index: 10;
  text-indent: -99999px;
  background-size: 100%;
}
@media (max-width: 1100px) and (min-width: 761px) {
  .container__logo-nps {
    top: 24px;
    left: 20px;
    width: 205px*.7;
    height: 44px*.7;
  }
}
@media (max-width: 760px) {
  .container__logo-nps {
    top: 20px;
    left: 10px;
    width: 103px;
    height: 23px;
  }
}